.single-post {
	font-size: 1.25rem; /* 20px */
	line-height: 1.8;

	@include atSmall {
		font-size: 1rem; /* 16px */
	}

	h1 {
		font-size: 3rem; /* 48px */

		@include atSmall {
			font-size: 2.25rem; /* 36px */
		}
	}

	h1.kead-h1-small {
		font-size: 2.4rem;

		@include atSmall {
			font-size: 1.8rem;
		}
	}

	h2 {
		font-size: 2.5rem; /* 40px */
		line-height: 1;
		font-weight: 400;
		margin-top: 1rem;

		@include atSmall {
			font-size: 2rem; /* 32px */
		}
	}

	h4 {
		font-size: 1.75rem; /* 28px */

		@include atSmall {
			font-size: 1.5rem; /* 24px */
		}
	}

	h5 {
		font-size: 1.5rem; /* 24px */

		@include atSmall {
			font-size: 1.125rem; /* 18px */
		}
	}

	p.kead-caption {
		font-size: 0.875rem; /* 14px */

		@include atSmall {
			font-size: 0.75rem; /* 12px */
		}
	}

	img {
		max-width: 100%;
		width: auto;

		@include atMedium {
			width: 100%;
		}
	}

	figure {
		margin: 0;
		height: 100%;

		img {
			height: 100%;
			object-fit: cover;
		}
	}

	&__contents {
		ul,
		li {
			list-style: disc;
		}
	}
}
